"use client";
import { useState } from "react";
import { PersonalInfo,
        ContactInfo,
        Education,
        Experience,
        SkillsAwards } from "./forms";
import { FormValues } from "../types";
import { FiUser,
        FiPhone,
        FiBookOpen,
        FiBriefcase,
        FiAward,
        FiPlus,
        FiLogOut,
        FiSettings } from "react-icons/fi";
import { GoArrowSwitch } from "react-icons/go";


export default function Editor() {

    //------------FORMS---------------
    const[formData, setFormData] = useState({
        personalInfo: {
            firstName: "",
            middleName: "",
            lastName: "",
            desiredJob: "",
        },
        contactInfo: {
            phone: "",
            email: "",
            country: "",
            city: "",
            street: "",
            zip: "",
        },
        educationInfo: {
            institution: "",
            degree: "",
            startDate: "",
            endDate: "",
            desc: "",
        },
        experienceInfo: {
            jobTitle: "",
            company: "",
            startDate: "",
            endDate: "",
            desc: "",
        }
    })

    //ChatGPT gave me this horrible triple nested function but it works so
    function handlePersonalInputChange(field: keyof FormValues['personalInfo']): React.ChangeEventHandler<HTMLInputElement> {
        return function (e: React.ChangeEvent<HTMLInputElement>): void {
          setFormData(function (prev: FormValues) {
            console.log(`Previous value for ${field}:`, prev.personalInfo[field]);
            const newState = {
              ...prev,
              personalInfo: {
                ...prev.personalInfo,
                [field]: e.target.value,
              },
            };
            console.log("New state:", newState);
            return newState;
          });
        };
      }

    function handleContactInputChange(field: keyof FormValues['contactInfo']) {
        return function (e: React.ChangeEvent<HTMLInputElement>) {
          setFormData(function (prev: FormValues) {
            return {
              ...prev,
              contactInfo: {
                ...prev.contactInfo,
                [field]: e.target.value,
              },
            };
          });
        };
    };

    function handleEducationInputChange(field: keyof FormValues['educationInfo']) {
        return function (e: React.ChangeEvent<HTMLInputElement>) {
          setFormData(function (prev: FormValues) {
            return {
              ...prev,
              educationInfo: {
                ...prev.educationInfo,
                [field]: e.target.value,
              },
            };
          });
        };
    };

    function handleExperienceInputChange(field: keyof FormValues['experienceInfo']) {
        return function (e: React.ChangeEvent<HTMLInputElement>) {
          setFormData(function (prev: FormValues) {
            return {
              ...prev,
              experienceInfo: {
                ...prev.experienceInfo,
                [field]: e.target.value,
              },
            };
          });
        };
    };

    function handleSubmit(e: React.FormEvent) {
        e.preventDefault();
        console.log('Form Data: ', formData)
    }
    

    //-----------BUTTONS--------------
    const[toggle, setToggle] = useState(<PersonalInfo values={formData.personalInfo} onInputChange={handlePersonalInputChange}/>);
    const[activeButton, setActiveButton] = useState("personalInfo");
    const[disabled, setDisabled] = useState("personalInfo")

    function activateButton(name: string){
        setActiveButton(name);
        setDisabled(name);

        switch (name) {
            case "personalInfo":
                return setToggle(<PersonalInfo values={formData.personalInfo} onInputChange={handlePersonalInputChange}/>)
            case "contactInfo":
                return setToggle(<ContactInfo values={formData.contactInfo} onInputChange={handleContactInputChange}/>)
            case "education":
                return setToggle(<Education values={formData.educationInfo} onInputChange={handleEducationInputChange}/>)
            case "experience":
                return setToggle(<Experience values={formData.experienceInfo} onInputChange={handleExperienceInputChange}/>)
            case "skillsAwards":
                return setToggle(<SkillsAwards />)
        }
    }

    //-------------HTML--------------------
    return(
        <div className="grid grid-cols-7 w-screen h-screen bg-darkGray p-[6px]">
            {/*Left side*/}
            <div className="col-span-2 flex flex-row rounded-[6px] bg-mediumGray">
                {/*Buttons*/}
                <div className="flex flex-col p-[6px] w-[15%]">
                    {/*Top*/}
                    <div className="flex flex-col justify-start items-center w-full h-full gap-[6px]">
                        {/*Personal Info*/}
                        <button onClick={() => activateButton("personalInfo")}
                                disabled={disabled === "personalInfo" ? true : false}
                                className={`flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100 
                                ${activeButton === "personalInfo" ? "bg-highlightGray" : ""}`}>
                            <FiUser className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Contact Info*/}
                        <button onClick={() => activateButton("contactInfo")}
                                disabled={disabled === "contactInfo" ? true : false}
                                className={`flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100 
                                ${activeButton === "contactInfo" ? "bg-highlightGray" : ""}`}>
                            <FiPhone className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Education*/}
                        <button onClick={() => activateButton("education")}
                                disabled={disabled === "education" ? true : false}
                                className={`flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100 
                                ${activeButton === "education" ? "bg-highlightGray" : ""}`}>
                            <FiBookOpen className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Work Experience*/}
                        <button onClick={() => activateButton("experience")}
                                disabled={disabled === "experience" ? true : false}
                                className={`flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100 
                                ${activeButton === "experience" ? "bg-highlightGray" : ""}`}>
                            <FiBriefcase className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Skills/Awards*/}
                        <button onClick={() => activateButton("skillsAwards")}
                                disabled={disabled === "skillsAwards" ? true : false}
                                className={`flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100 
                                ${activeButton === "skillsAwards" ? "bg-highlightGray" : ""}`}>
                            <FiAward className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Add Section*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] border-[2px] border-highlightGray p-[10px] hover:bg-highlightGray transition-bg duration-100">
                            <FiPlus className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                    </div>
                    {/*Bottom*/}
                    <div className="flex flex-col items-center w-full h-fit gap-[6px]">
                        {/*Swap*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <GoArrowSwitch className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Settings*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiSettings className="text-offWhite w-[25px] h-[25px]"/>
                        </button>
                        {/*Exit*/}
                        <button className="flex justify-center w-fit h-fit rounded-[4px] p-3 hover:bg-highlightGray transition-bg duration-100">
                            <FiLogOut className="text-lightRed scale-x-[-1] w-[25px] h-[25px]"/>
                        </button>
                    </div>
                </div>
                {/*Line*/}
                <div className="bg-darkGray w-[4px] bg-darkGray"></div>
                {/*Content*/}
                <form className="flex flex-col w-full h-[calc(100vh-12px)] overflow-scroll justify-start items-center gap-[10px] px-2 py-4"
                        onSubmit={handleSubmit}>
                    { toggle }
                </form>
            </div>
            {/*Middle*/}
            <div className="col-span-3">

            </div>
            {/*Right Side*/}
            <div className="col-span-2 bg-white">

            </div>
        </div>
    )
}