function InputContainer({ children }: { children: React.ReactNode }){
    return(
        <div className="flex flex-col w-full h-fit py-1">
            { children }
        </div>
    )
}

function Title({ title }: { title: string }){
    return(
        <div className="flex flex-col justify-center items-start w-full h-fit gap-[2px] px-4">
            <p className="text-[20px] text-offWhite/90 font-bold">{ title }</p>
            <div className="w-[50%] h-[2px] bg-inputGray rounded-[1px]"></div>
        </div>
    )
}



